<template>
  <div class="form">
    <header>
      <h2>MCOC进攻英雄评级（05.2018）</h2>
      <p v-for="need in need" :key="need.name">
        <span :style="{ color: need.color }">{{ need.icon }}</span> {{ need.desc}}
      </p>
      <br>
    </header>

    <main>
      <HeroGroup
        v-for="item in rank"
        :key="item.lv"
        :rankLv="item.name"
        :rankName="item.des"
        :bcr="item.lv"
      />
    </main>

    <footer>
      <p>评级：Seatin</p>
      <p>制作：旅者黄小七</p>
      <p>注：本表暂不包含多米诺、屠杀、金侍</p>
    </footer>
  </div>
</template>

<script>
import HeroGroup from './HeroGroup.vue'

export default {
  name: 'HelloWorld',
  data () {
    return {
      need: this.GLOBAL.need,
      rank: [
        {lv: 1, name: '一线', des: '神级'},
        {lv: 2, name: '二线', des: '顶级半神级'},
        {lv: 3, name: '三线', des: '半神级'},
        {lv: 4, name: '四线', des: '惊艳级'},
        {lv: 5, name: '五线', des: '优秀级'},
        {lv: 6, name: '六线', des: '普通级'},
        {lv: 7, name: '七线', des: '较差级'}
      ]
    }
  },
  components: { HeroGroup }
}
</script>

<style lang="sass" scoped>
h3
  span
    font-size: 0.6rem

.form
  padding: 40px 0 40px 40px

  p
    span
      display: inline-block
      width: 1rem
      text-align: center
      margin-right: 0.5rem
  .rank
    display: flex
    flex-wrap: wrap

    .card
      display: flex
      flex-direction: column
      align-items: center
      margin-right: 10px
      -webkit-box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
      -moz-box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
      box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
      margin-bottom: 0.5rem
      div
        width: 66px
        height: 1.4rem
        line-height: 1.4rem
        text-align: center
      img
        background: rgb(54,19,1)
        background: linear-gradient(356deg, rgba(54,19,1,1) 0%, rgba(134,97,0,1) 100%)
        width: 60px
        height: 60px
footer
  margin-top: 2rem
  margin-right: 40px
  border-top: 1px solid #111
  padding-top: 1rem
  p
    margin: 0
    margin-bottom: 0.3rem
</style>



