<template>
  <div class="card">
    <img
      :src="imgSrc"
      :alt="heroName"
      :style="'border: 3px solid ' + bColor"
    >
    <div>
      <span style="color: Red" v-if="isAwakened">★</span>
      <span style="color: OrangeRed" v-if="needHSL">❖</span>
      <span style="color: Crimson" v-if="needSynergy">♥</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Hero',
  props: {
    borderColor: String,
    isAwakened: Boolean,
    needHSL: Boolean,
    needSynergy: Boolean,
    imgSrc: String,
    heroName: String
  },
  data () {
    return {
      need: this.GLOBAL.need,
      bColor: this.borderColor
    }
  }
}
</script>

<style lang="sass" scoped>
.card
  display: flex
  width: 66px
  flex-direction: column
  align-items: center
  margin-right: 10px
  -webkit-box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
  -moz-box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
  box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
  margin-bottom: 0.5rem
  div
    width: 66px
    height: 1.4rem
    line-height: 1.4rem
    text-align: center
  img
    background: rgb(54,19,1)
    background: linear-gradient(356deg, rgba(54,19,1,1) 0%, rgba(134,97,0,1) 100%)
    width: 60px
    height: 60px
</style>



