<template>
  <div>
    <h2>{{ rankLv }}（{{ rankName }}）</h2>
    <div class="rank">
      <Hero
        v-for="hero in champions"
        :key="hero.name"
        :imgSrc="'static/img/champion/' + hero.class + '/' + hero.name + '.png'"
        v-if="hero.BCR === bcr"
        :isAwakened="hero.isAwakened"
        :needHSL="hero.needHSL"
        :needSynergy="hero.needSynergy"
        :borderColor="hero.color"
      />
    </div>
  </div>
</template>

<script>
import Hero from './Hero'

export default {
  name: 'HeroGroup',
  props: {
    rankLv: String,
    rankName: String,
    bcr: Number
  },
  data () {
    return {
      champions: this.GLOBAL.champions
    }
  },
  components: { Hero }
}
</script>

<style lang="sass" scoped>
.rank
  display: flex
  flex-wrap: wrap
</style>



